@font-face {
  font-family  : 'RobotoSlab';
  font-style   : normal;
  font-weight  : 700;
  src          : local('Roboto Slab Bold'), local('RobotoSlab-Bold'), url("https://fonts.gstatic.com/s/robotoslab/v7/BngRUXZYTXPIvIBgJJSb6u92w7CGwR2oefDo.woff2") format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}
@font-face {
  font-family  : 'RobotoSlab';
  font-style   : normal;
  font-weight  : 400;
  src          : local('Roboto Slab Regular'), local('RobotoSlab-Regular'), url("https://fonts.gstatic.com/s/robotoslab/v7/BngMUXZYTXPIvIBgJJSb6ufN5qWr4xCC.woff2") format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}
@font-face {
  font-family  : 'Roboto';
  font-style   : normal;
  font-weight  : 400;
  src          : local('Roboto'), local('Roboto-Regular'), url("https://fonts.gstatic.com/s/roboto/v18/KFOmCnqEu92Fr1Mu4mxKKTU1Kg.woff2") format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}
@font-face {
  font-family  : 'Roboto';
  font-style   : normal;
  font-weight  : 700;
  src          : local('Roboto Bold'), local('Roboto-Bold'), url("https://fonts.gstatic.com/s/roboto/v18/KFOlCnqEu92Fr1MmWUlfBBc4AMP6lQ.woff2") format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}
$font-heading: 'RobotoSlab';
$font-text: 'Roboto';

html {
  font-size: 16px;
}

body {
  font-size  : 1rem;
  font-family: Helvetica, Arial, sans-serif;
  line-height: 1.5;
  color      : $color-text;
}

.fonts-loaded {
  body {
    font-family: $font-text;
  }

  h1,
  h2,
  h3,
  h4,
  h5 {
    font-family: $font-heading;
  }
}

h1,
h2,
h3,
h4,
h5 {
  font-family   : serif;
  font-weight   : 700;
  text-transform: uppercase;
  color         : $color-heading;
}

h3 {
    line-height: 1.2 !important;
}

h6 {
  color      : $color-heading;
  font-weight: 700;
}

p {
  margin     : 0 0 0.5em;
  line-height: 1.5;
}

strong {
  font-weight: 700;
}

.light-text {
  font-weight: 300;
}

address {
    font-style: normal;
}
