// Text meant only for screen readers. Addresses part of theme-check issue #840.
// https://github.com/Automattic/_s/blob/master/style.css

.screen-reader-text {
  clip    : rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height  : 1px;
  width   : 1px;
  overflow: hidden;
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius   : 3px;
  box-shadow      : 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip            : auto !important;
  color           : #21759b;
  display         : block;
  font-size       : 14px;
  font-size       : 0.875rem;
  font-weight     : bold;
  height          : auto;
  left            : 5px;
  line-height     : normal;
  padding         : 15px 23px 14px;
  text-decoration : none;
  top             : 5px;
  width           : auto;
  z-index         : 100000; // Above WP toolbar.
}
// Fix the issue where the WP admin-bar overlaps the mobile menu

#wpadminbar {
  position: fixed !important;
}
// Make sure that the WP admin-bar does not overlap the sticky top bar

body.admin-bar.f-topbar-fixed {
  .sticky.fixed {
    margin-top: rem-calc(32);
  }
}

.wp-caption > figcaption {
  max-width: 100%;
  font-size: 0.8rem;
  color    : #999;
  padding  : 0.25rem 0;
}

p.wp-caption-text {
  font-size: 90%;
  color    : #666;
  padding  : rem-calc(10) 0;
}

.alignleft {
  float        : left;
  padding-right: 1rem;
  margin       : 0;
}

.alignright {
  float       : right;
  padding-left: 1rem;
  margin      : 0;
}

.aligncenter {
  display     : block;
  margin-left : auto;
  margin-right: auto;
}

body#tinymce {
  height   : auto; // Fix editor style bug
  max-width: $grid-row-width; // Give the editor a max-width
  padding  : rem-calc(20) !important;
}

.zopim {
  position: fixed !important;
  right   : 5px;
  bottom  : 0;
  z-index : 1!important;
}

