.bottom {
  &-15 {
    margin-bottom: 15px;
  }
}

.top {
  &-15 {
    margin-top: 15px;
  }
}

.bg {
  &-grey {
    background-color: $color-grey;
  }

  &-footer {
    background-color: $color-footer-bg;
  }
}

ul {
  margin         : 0;
  list-style-type: none;
}

.capitalize {
  text-transform: capitalize;
}

.no-text-transform {
  text-transform: none;
}

.relative {
  position: relative;
}

img.rounded {
  border-radius: 50%;
}

.styled-list > li {
  position     : relative;
  padding-left : 1em;
  margin-bottom: 0.5em;

  &:before {
    content : "\2022";
    position: absolute;
    top     : 1px;
    left    : 0;
    color   : $color-cta;
  }
}

.text-style {
  ul {
    list-style-type: none;
    @extend .styled-list;
  }
}
