.blur-up {
  filter: blur(5px);
  transition: filter 400ms;

  &.lazyloaded {
    filter: blur(0);
  }
}

.responsive-embed {
  padding-bottom: 56.25%;
}

iframe {
  border      : none!important;
  border-color: transparent!important;
}

/* Contact form */
span.wpcf7-list-item {
    margin: 0 !important;
}

.wpcf7-submit {
  background-color: transparent
}
